* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
.page{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  max-width: 500px;
  padding: 0 20px;

  left: 50%;
  transform: translate(-50%, 0);
  background-color: #f7f7f7;

  display: flex;
  flex-direction: column;
}

.bottomButton {
  width: 100%;
  height: 48px;
  border: none;
  font-weight: 700;
  background-color: #808080;
}


/* 사이드바 */
.container{
  display:flex;
}
main{
  width: 100%;
  padding: 20px;
}

.sidebar{
  background: #808080;
  color: #fff;
  height: 100vh;
  width: 240px;
}

.top_section{
  display: flex;
  align-items: center;
  padding: 0px 15px;
  
}

.logo {
  font-size: 30px;
  margin-top: 10px;
}

.bars {
  font-size: 40px;
  padding: 10px 10px;
  margin-top: 20px;
  margin-bottom: 0px;
}

.link {
  display: flex;
  color: #000;
  padding: 12px 25px;
  margin-top: 0px;
  gap: 10px;
  transition: all 0.5s;
}

.link:hover{
  background: #BFDAFF;
  color: #0055CC;
  transition: all 0.5s;
}

.active{
  background: #BFDAFF;
  color: #0055cc;
}

.icon, .link_text{
  font-size: 20px;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

div {
  box-sizing:  border-box;
}
.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
